.header {
  box-sizing: border-box;
  height: 100vh;
  background-image: url(../image/header-bg-mobile.png);
  background-size: cover;
  background-position: center;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &__title {
    margin: 0;
    font-weight: 800;
    font-size: 32px;
    line-height: 100%;
    color: #fff;
  }

  &__bottom {
    padding-bottom: 40px;
    @include constentPaddingInline;
  }
}

